import React, { useState, useEffect } from 'react';
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import styled from 'styled-components'

//  
import slider1 from '../../assets/img/slider/slider1.webp';
import slider2 from '../../assets/img/slider/slider2.webp';

// slider2  
import slider2Mobile from '../../assets/img/slider/slider_img_mobile.webp';
import SliderImgContainer from './SliderImgContainer/SliderImgContainer';


export default function HomeSlider(){
    const[reHeight, setreHeight] = useState(0)
    const settings = {
        dot: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrow: true,
        autoplay: true,
        autoplaySpeed: 2000,
    };

    useEffect(() => {
        const handleResize = () => {
            setreHeight(window.innerHeight - 93);
        };
  
        handleResize();
        window.addEventListener("resize", handleResize);
  
        return () => {
         window.removeEventListener("resize", handleResize);
        };
    }, []);

    return (
        <SliderContainer reHeight={reHeight}>
            <Slider {...settings}>
                {/* slider1: /   */}
                <SliderImgContainer 
                  src={slider1}
                  alt={''}
                  subtitle="PREMIUM"
                  title="   ,<br/>   ,<br/>   "
                  showPhone={false}
                  isBuildingSlide={true}
                />
                
                {/* slider2:    */}
                <SliderImgContainer 
                  src={slider2}
                  srcMobile={slider2Mobile}  //    
                  alt={''}
                  subtitle=" AK PRUGIO"
                  title="   ,<br/>  "
                  phoneLabel="  "
                  phoneNumber="1661-3822"
                  showPhone={true}
                  isBuildingSlide={false}
                />
            </Slider>
        </SliderContainer>
    )
}

const SliderContainer = styled.div`
    width: 100%;
    overflow: hidden;
    
    .slick-slider,
    .slick-list,
    .slick-track,
    .slick-slide,
    .slick-slide > div {
        height: 100%;
    }
`